  --light_gray: rgb(134, 134, 134);
  --warning: #fa9e34;
  --radius: 2.5vh;
  /*about us*/

  --wave_color: white;
  --card_header_height: 7vh;
  --card_shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  --btn_shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
  --header_shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  --horizontal_scroll_shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;

  /* chat room*/
