  --s1_5: 1.5vh;
  --s2_5: 2.5vh;
  --s3_5: 3.5vh;
  --s4_5: 4.5vh;
  --s5_5: 5.5vh;
  --s6_5: 6.5vh;
  --s7_5: 7.5vh;
