.cc {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.first_100vh_bk {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: var(--main_light);
  transition: all 0.6s ease;
  position: relative;
}
.first_100vh_bk_purple {
  composes: first_100vh_bk;
  height: 140%;
  background: var(--sub_light);
}

.svg {
  width: 100%;
  height: 6%;
}

.svg2 {
  position: absolute;
  top: 0;
  width: 100%;
  height: 4%;
  z-index: 0;
}

.content {
  width: 100%;
  height: 85%;
  display: flex;
  justify-content: center;
  align-items: center;

  margin-top: 6vh;
}
.content_back {
  width: 100%;
  height: 89%;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 6vh;
}

.flip_container {
  width: 80%;
  height: 100%;
  perspective: 5000px;
}

.two_layer_card {
  position: absolute;
  transform-style: preserve-3d;
  transition: all 1.2s ease;
  width: 100%;
  height: 100%;
  background: var(--white);
  /* background: radial-gradient(ellipse at center, var(--white) 0%, black 2000%); */
  border-radius: 25px;
  box-shadow: var(--card_shadow);
}
.two_layer_card_rotate {
  composes: two_layer_card;
  transform: rotateY(180deg);
}
.card_front {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  z-index: -1;
}
.card_back {
  composes: card_front;
  height: 100%;
  transform: rotateY(180deg);
}
.low1_initial {
  width: 100%;
  height: calc(var(--card_header_height) + 0%);
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  background: var(--white);
}
.low1 {
  width: 100%;
  height: calc(var(--card_header_height) + 0%);
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  background: var(--white);
}

.up1_init {
  composes: cc;
  position: relative;
  justify-content: space-evenly;
  width: 100%;
  height: 100%;
  background: var(--main);
  border-radius: 25px;
  border-bottom-left-radius: 0px;
  font-size: 1.7vh;
}
.up1 {
  composes: up1_init;
  animation: change_switch_to_main_color 1.2s ease forwards;
}
.up1_back {
  composes: up1_init;
  animation: change_switch_to_sub_color 1.2s ease forwards;
}
@keyframes change_switch_to_main_color {
  0% {
    background: var(--sub);
  }
  95% {
    background: var(--main);
  }
  100% {
    background: var(--main);
  }
}
@keyframes change_switch_to_sub_color {
  0% {
    background: var(--main);
  }
  95% {
    background: var(--sub);
  }
  100% {
    background: var(--sub);
  }
}

.low1_back {
  composes: low1;
  background: var(--white);
  animation: change_back_color_back 1.2s ease forwards;
}
@keyframes change_back_color_back {
  0% {
    background: var(--white);
  }
  95% {
    background: var(--white);
  }
  100% {
    background: var(--white);
  }
}

.wrap {
  composes: cc;
  position: relative;
}
.btn1 {
  composes: cc;
  animation: shrink 0.4s ease forwards;
  cursor: pointer;
}
@keyframes shrink {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.7);
  }
}
.btn1_initial {
  composes: cc;
  animation: enlarge_initial 0.84s ease forwards;
  cursor: pointer;
}
@keyframes enlarge_initial {
  0% {
    transform: scale(0.7);
  }

  100% {
    transform: scale(1);
  }
}

.btn1_active {
  composes: cc;
  animation: enlarge 1.2s ease forwards;
  cursor: pointer;
}

@keyframes enlarge {
  0% {
    transform: scale(0.7);
  }
  30% {
    transform: scale(0.7);
  }
  100% {
    transform: scale(1);
  }
}
.moving_bar {
  position: absolute;
  height: 8%;
  /* border-style: solid; */
  border-color: var(--white);
  bottom: -25%;
  background: var(--white);
  animation: longer 1.2s ease forwards;
}
.moving_bar_initial {
  composes: moving_bar;
  animation: longer_initial 0.84s ease forwards;
}
@keyframes longer_initial {
  0% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}
.moving_bar_back {
  composes: moving_bar;
  border-color: var(--white);
  background: var(--white);
}
@keyframes longer {
  0% {
    width: 50%;
  }
  30% {
    width: 50%;
  }
  100% {
    width: 100%;
  }
}
.font {
  margin-left: 1vh;
  color: var(--gray);
}
.font_active {
  margin-left: 1vh;
  font-weight: bold;
  color: var(--white);
}
.font_active_back {
  composes: font_active;
  color: var(--white);
}

.svg1 {
  width: var(--s3_5);
  height: var(--s3_5);
}
.svg1_active {
  composes: svg1;
}

.svg1_active_back {
  composes: svg1_active;
}

.about_svg {
  width: var(--s3_5);
  height: var(--s3_5);
}

.low2_init {
  position: relative;
  width: 100%;
  background: var(--main);
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}
.low2 {
  composes: low2_init;
  animation: change_low2_to_main_color 1.2s ease forwards;
}
.low2_back {
  composes: low2_init;
  animation: change_low2_to_sub_color 1.2s ease forwards;
}
@keyframes change_low2_to_main_color {
  0% {
    background: var(--sub);
  }
  95% {
    background: var(--main);
  }
  100% {
    background: var(--main);
  }
}
@keyframes change_low2_to_sub_color {
  0% {
    background: var(--main);
  }
  95% {
    background: var(--sub);
  }
  100% {
    background: var(--sub);
  }
}
.this_content_initial {
  width: 100%;
  height: 100%;
  animation: enlarge_initial 0.84s ease forwards;
}
.this_content_active {
  width: 100%;
  height: 100%;
  animation: enlarge 1.2s ease forwards;
}

.this_content_inactive {
  width: 100%;
  height: 100%;
}
.up2_init {
  width: 100%;
  height: 100%;
  background: var(--white);
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}
.up2 {
  composes: up2_init;
  height: 100%;
}
.up2_back {
  composes: up2_init;
  height: 100%;
}
.content_view {
  composes: cc;
  height: 100%;
  width: 100%;
  flex-direction: column;
}
.logo_view {
  composes: content_view;
  height: auto; /*linked*/
}

.words_view {
  composes: cc;
  flex-direction: column;
  align-items: flex-start;
