.words {
  width: 100%;
}
.yt {
  composes: cc;
  justify-content: start;
  padding-top: 3vh;
  font-size: 1.7vh;
  margin-left: 2vh;
}
.ytSVG {
  margin-top: 0.2vh;
  margin-right: 0.6vh;
  margin-left: 0.15vh;
}
.mansion {
  composes: cc;
  justify-content: start;
  padding-top: 3vh;
  font-size: 1.7vh;
  margin-left: 2vh;
}
.mansionSVG {
  margin-right: 1vh;
}
.white_block {
  position: absolute;
  bottom: -4px;
  height: 2%;
  width: 100%;
  background: white;
}
